import React from 'react'
import Slider from 'react-slick'
import "babel-polyfill"
import home_slider_default from '../../lib/app/img/home_slider_pic_default.png'
import { getDate } from '../../lib/app/js/app'
import './index.less'

export default class HomeHeaderSlideShow extends React.Component {
  render() {
    let settings = {
      dots: true,
      infinite: true,
      autoplay: true,
      speed: 500,
      autoplaySpend: 100,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <div className="pc-home-header-slide-show">
        <Slider {...settings}>
          <div><img src={home_slider_default} className="slide-page" /></div>
          <div><img src={home_slider_default} className="slide-page" /></div>
          <div><img src={home_slider_default} className="slide-page" /></div>
          <div><img src={home_slider_default} className="slide-page" /></div>
          <div><img src={home_slider_default} className="slide-page" /></div>
        </Slider>
      </div>
    )
  }
  



async componentDidMount() {
  let res = await getDate('http://inwecryptocms.app/ad');
  console.log(res)
  return res
}

}




